
.row-2{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px 0 ;
    background-color: #F2F2F2;
    margin: 40px 0 0;
  }
  .row-2 h2{
    color: #3E6975;
  }
  .row-3{
    padding: 40px 0 ;
    background-color: #F2F2F2;
  }
  .list{
    margin: 0 auto;
    display: flex;
    list-style: none;
    justify-content: space-evenly;
  }
  .list .item{
    width: 25%;
    display: flex;
  }
  .list .item dt{
    height: 100px;
    display: flex;
  }
  .list .item dl{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .list .item dl .icon-box{
    padding: 30px 0;
    width: 100px;
  }
  .list .item dl .icon-box img{
    width: 100%;
  }
  .list .item dl .text{
    font-size: 16px;
  }
  