.header {
  width: 100%;
  height: 9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #d3edf5;
  padding: 0 10%;
}

.logo {
  padding: 100px 10px 100px 10px;
  border-radius: 60px;
}

.header ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: baseline;
}

.header li {
  margin-left: 3rem;
}

.header a {
  text-decoration: none;
  font-size: 1.1rem;
  color: #000000;
  transition: 0.2s;
  border-radius: 99px;
  border-width: 10rem;
}

.login {
  text-decoration: none;
  font-size: 1.1rem;
  color: #000000;
  transition: 0.2s;
  border-radius: 99px;
  border-width: 10rem; 
}

.login :hover,
.login :active,
.login a.active{
  font-size: 1.15rem;
  color: orange;
}

.sign{
  background-color: rgb(80, 180, 140);
}

.header a:hover,
.header a:active,
.header a.active {
  font-size: 1.15rem;
  color: orange;
}
