.item {
    margin: 1rem 0;
  }

.iconbox{
  width:100%;
  height: 10vh;
  object-fit: contain;
  margin-left: -25rem;
}
  
  .image {
    align-items: center;
    width: 100%; 
    height :20rem;
    overflow: hidden;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
  }
  
  .image img{
    padding: 5%;
    padding-left: 3%;
    width: 100%;
    object-fit: contain;
    height: 50vh;
  }
  
  .content{
    text-align: center;
    padding: 1rem;
  }
  
  .contnet h3{
    font-size: 1.25rem;
    color: #2c292b;
  }
  
  .actions {
    padding: 1.5rem;
    text-align: center;
  }
  
  .actions button{
    font:inherit;
    cursor: pointer;
    background-color: #00d095;
    color: white;
    border: 1px solid #00d095;
    border-radius: 4px;
    font-weight: bold;
    font-size: 1rem;
    width: 8rem;
    height: 3rem;
    margin-left: 5rem;
    margin-right: 5rem;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: rgb(90, 204, 143);
    border-color: rgb(90, 204, 143);
    color:#2c292b
  }
  